import React from 'react';
import axios from 'axios'

const Logout = (props) => {

    let request = axios.get('/api/logout')
    .then(request => {
        setTimeout(()=>{
            props.history.push('/')
        },2000)
    })
    return(
    <div className = 'logout_container'>
        <h2>
            sorry to see you go :(
        </h2>
    </div>
    )

};

export default Logout;



